* {
  margin: 0;
  padding: 0;
}

.body {
  background-color: #212529;
}
/* Detail */

.title {
  /* font-size: calc(8px + 2.5vmin); */
  font-size: 1.7rem;
  /* font-size: calc(1.7rem + 0.5vmin); */
  color: turquoise;
  /* padding-left: 1.5rem; */
  /* padding-bottom: none; */
  z-index: 1;
}



.title2 {
  font-size: 1.7rem;
  color: turquoise;
  opacity: 1 !important;
  /* padding-bottom: 4px;
  margin-right: 3px; */
}

.subtitle {
  font-size: 1.2rem;
  color: #ccc;
}






.badge {
  /* position: r;
  top: 40px;
  right: 0px; */
}


.box {
  /* border: 2px solid currentColor; */
  border: 1px solid #2a3d3d;
  border-radius: 5px;
  text-align: center;
  align-items: center;
  display: block;
  justify-content: center;
  background: #212529;
  /* box-shadow: 2px 2px 2px 1px  rgb(0 0 0 / 30%); */
}


.box-left {
  /* border: 1px solid #2a3d3d; */
  border-radius: 5px;
  text-align: left;
  align-items: center;
  display: block;
  justify-content: space-evenly;
  background: #212529;
  /* box-shadow: 2px 2px 2px 1px  rgb(0 0 0 / 30%); */
}

/* Sidebar */

.sidebar {
  width: 100%;
  height: 0vh;
  display: flex;
  flex-wrap: wrap;
}



.side {
  background: #212529;
  opacity: 0.98;
  min-height: 100vh;
  font-size: calc(20px + 2.5vmin);
  /* color: rgb(45, 182, 130); */
  /* border: 1px solid turquoise; */
  /* border-radius: 5px; */
  overflow: hidden;
  /* width: auto; */
  box-shadow: 2px 2px 2px 1px  rgb(0 0 0 / 10%);
}

.visible {
  z-index: 10;
}

.hidden {
  display: none;
}

@media only screen and (min-width: 913px) {
  .side {
    width: 20vw;
  }
}

@media only screen and (min-width: 1560px) {
  .side {
    width: 15vw;
  }
  .map-overlay {
    width: 15vw;
  }

}



/* Buttons */

.button {
 height: 3.5rem;
 width: 6rem;
  z-index: 2;
  color: rgb(31, 174, 160) !important;
  outline: 1px solid rgb(31, 174, 160) !important;
  margin: 5px;
}

.button:focus {
  background-color: rgb(31, 174, 160) !important;
}

.close {
  height: 1.7rem;
  width: 1.7rem;
  /* margin-right: 19px; */
  /* margin-top: 10px; */
  padding: 10px;
  color: rgb(31, 174, 160) !important;
  outline: 1px solid rgb(31, 174, 160) !important;
  opacity: 1 !important;
}

.sort {
  height: 2rem;
  width: 4.3rem;
  /* font-size: 2.7em; */
  /* margin-bottom: 10px; */
  /* margin-top: 10px; */
  /* padding: 0px 0px 0px 0px; */
  /* color:#fff; */
  /* outline: 1px solid rgb(31, 174, 160) !important; */
  /* vertical-align: middle; */
  margin: 0px;
}

/* .activ {
  color: "1px solid orange" !important;
} */

.button:hover {
  background-color: rgb(31, 174, 160) !important;
  color: #fff !important;
}

.button:focus {
  /* background-color: rgb(31, 174, 160) !important; */
  color: #fff !important;
}

.number {
  font-family: "Baloo Thambi 2", cursive;
}

/* Tabs */

/* .wrapper {
  margin: 5px 0px;
} */

.tabs {
  display: flex;
  justify-content: left;
  /* padding-top: 0.7rem; */
}

.tabitem {
  cursor: pointer;
  height: 0px;
  text-align: left;
  /* padding: 5px; */
  padding-right: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  /* margin-right: 30px; */
}

/* .icon {
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
} */


.tabitem--inactive {
  opacity: 0.5;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
}

.tabitem--inactive:hover {
  opacity: 1;
}

.tabitem__icon {
  color: rgba(255, 255, 255, 0.9);
  font-size: 8px;
}

.tabitem__title {
  color: rgba(255, 255, 255, 0.9);
  font-size: 14px;
}

.content {
  text-align: center;
  /* margin-top: 5px; */
}

